<template>
  <div class="comment-template">
    <div class="comment-template__header">
      <p class="comment-template__name"></p>
      <a-popconfirm
        title="?"
        placement="left"
        @confirm="() => removeWidget({ widgetId: activeWidget.widgetId })"
        okText=""
        cancelText=""
      >
        <a-button shape="circle" type="danger" icon="delete" />
      </a-popconfirm>
    </div>
    <div class="lines-config">
      <a-tabs
        defaultActiveKey="1"
        tabPosition="top"
        :style="{ height: '100%'}"
      >
        <a-tab-pane tab="" key="1">

          <!-- S  -->
          <CommonTemplate />
          <!-- E  -->

        </a-tab-pane>

        <a-tab-pane tab="" key="2">

          <!-- S  -->
          <ChartProprietaryTemplate ref="chartProprietaryRef">
            <template v-slot:header="{ config }">

              <!-- S  -->
              <a-collapse defaultActiveKey="1" :bordered="false">

                <a-collapse-panel header="" key="1">

                  <div class="comment-template__item">
                    <p class="comment-template__leading">:</p>
                    <div class="comment-template__inner">
                      <a-select
                        v-model="config.proprietaryConfig.lineStyle.type"
                        @change="$refs.chartProprietaryRef.change()">
                        <a-select-option value="solid"></a-select-option>
                        <a-select-option value="dashed"></a-select-option>
                        <a-select-option value="dotted"></a-select-option>
                      </a-select>
                    </div>
                  </div>
                  <!-- /  -->

                  <div class="comment-template__item">
                    <p class="comment-template__leading">:</p>
                    <div class="comment-template__inner comment-template__end">
                      <a-switch
                        checkedChildren=""
                        unCheckedChildren=""
                        v-model="config.proprietaryConfig.smooth"
                        @change="$refs.chartProprietaryRef.change()" />
                    </div>
                  </div>
                  <!-- /  -->

                  <div class="comment-template__item">
                    <p class="comment-template__leading">:</p>
                    <div class="comment-template__inner">
                      <a-slider
                        v-model="config.proprietaryConfig.lineStyle.width"
                        @change="$refs.chartProprietaryRef.change()"
                        :min="1"
                        :max="10" />
                    </div>
                  </div>
                  <!-- /  -->

                  <div class="comment-template__item">
                    <p class="comment-template__leading">:</p>
                    <div class="comment-template__inner">
                      <ColorPicker
                        v-model="config.proprietaryConfig.lineStyle.color"
                        @change="$refs.chartProprietaryRef.change()"/>
                    </div>
                  </div>
                  <!-- /  -->

                </a-collapse-panel>
                <!-- /  -->

              </a-collapse>
              <!-- E  -->

            </template>
          </ChartProprietaryTemplate>
          <!-- E  -->

        </a-tab-pane>

        <a-tab-pane tab="" key="3">

          <!-- S  -->
          <DataSourceTemplate />
          <!-- E  -->

        </a-tab-pane>
      </a-tabs>
    </div>
  </div>
</template>

<script>
import '@/assets/less/template.less'
import { mapState, mapMutations } from 'vuex'
import { ScreenMutations } from '@/store/modules/screen'
import CommonTemplate from '../common/index.vue'
import ChartProprietaryTemplate from '../chartProprietary/index.vue'
import ColorPicker from '@/components/colorPicker/index.vue'
import DataSourceTemplate from '../dataSource/index.vue'

export default {
  name: 'Lines',
  components: {
    CommonTemplate,
    ChartProprietaryTemplate,
    ColorPicker,
    DataSourceTemplate
  },
  computed: {
    ...mapState('screen', ['activeWidget'])
  },
  methods: {
    ...mapMutations('screen', {
      removeWidget: ScreenMutations.REMOVE_WIDGET
    })
  }
}
</script>

<style scoped>
</style>
