import anime from 'animejs'
import echarts from 'echarts'
import _ from 'lodash'

export default class Chart {
  constructor ({ widget }) {
    this.container = document.getElementById(widget.widgetId)
    this.chartConfig = {}
    this.setContainer(widget)
    this.init(widget)
  }

  /**
   * 
   * @param config
   */
  setContainer ({ config }) {
    const {
      width, height, top, left, zIndex
    } = config.commonConfig
    anime.set(this.container, {
      width,
      height,
      top,
      left,
      zIndex
    })
  }

  /**
   * 
   * @param widget
   */
  init ({ config }) {
    this.chart = echarts.init(this.container, '', {
      renderer: 'canvas'
    })
    this.mergeOption(config)
  }

  /**
   * resize
   */
  resize () {
    this.chart.resize()
  }

  /**
   * 
   */
  reset () {
    this.chart.setOption({})
  }

  /**
   *  echarts 
   */
  // eslint-disable-next-line class-methods-use-this
  mappingOption () {}

  /**
   * 
   * @param config widget 
   */
  mergeOption (config) {
    //  echarts 
    this.chartConfig = this.mappingOption(config)
    // 
    if (_.isEmpty(this.chartConfig.series)) {
      this.chart.clear()
    }
    // 
    this.chart.setOption(this.chartConfig)
  }

  /**
   * 
   */
  destroy () {}
}
