<template>
  <div class="common-template">

    <a-collapse defaultActiveKey="1" :bordered="false">

      <!-- S  -->
      <a-collapse-panel header="" key="1">

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <ColorPicker
              v-model="config.commonConfig.backgroundColor"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

      </a-collapse-panel>
      <!-- E  -->

      <!-- S  -->
      <a-collapse-panel header="" key="2">

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-slider
              :min="0"
              :max="16"
              @change="change('style')"
              v-model.number="config.commonConfig.border.width" />
          </div>
        </div>
        <!-- /  -->

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <ColorPicker
              v-model="config.commonConfig.border.color"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

      </a-collapse-panel>
      <!-- E  -->

      <!-- S  -->
      <a-collapse-panel header="" key="3">

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              min="0"
              v-model.number="config.commonConfig.padding[0]"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.padding[1]"
              min="0"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.padding[2]"
              min="0"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.padding[3]"
              min="0"
              @change="change('style')" />
          </div>
        </div>
        <!-- /  -->

      </a-collapse-panel>
      <!-- E  -->

      <!-- S  -->
      <a-collapse-panel header="" key="4">

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.width"
              min="0"
              @change="change('size', 'width')" />
          </div>
        </div>
        <!-- /  -->

        <div class="comment-template__item">
          <p class="comment-template__leading">:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.height"
              min="0"
              @change="change('size', 'height')" />
          </div>
        </div>
        <!-- /  -->

      </a-collapse-panel>
      <!-- E  -->

      <!-- S  -->
      <a-collapse-panel header="" key="5">

        <div class="comment-template__item">
          <p class="comment-template__leading">X:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.left"
              min="0"
              @change="change('position')" />
          </div>
        </div>
        <!-- / x -->

        <div class="comment-template__item">
          <p class="comment-template__leading">Y:</p>
          <div class="comment-template__inner">
            <a-input
              type="number"
              v-model.number="config.commonConfig.top"
              min="0"
              @change="change('position')" />
          </div>
        </div>
        <!-- / y -->

      </a-collapse-panel>
      <!-- E  -->

    </a-collapse>

  </div>
</template>

<script>
import '@/assets/less/template.less'
import _ from 'lodash'
import { mapState, mapGetters, mapMutations } from 'vuex'
import { ScreenMutations } from '@/store/modules/screen'
import ColorPicker from '@/components/colorPicker/index.vue'
import AdjustMixins from '@/components/wrapper/AdjustMixins.vue'

export default {
  name: 'CommonTemplate',
  mixins: [AdjustMixins],
  components: {
    ColorPicker
  },
  computed: {
    ...mapState('screen', [
      'activeWidget',
      'topologyEditable',
      'activeNode',
      'activeEdge',
      'edgeConfig'
    ]),
    ...mapGetters('screen', ['scale']),
    //  state.activeWidgetstate.activeWidget
    config () {
      return _.cloneDeep(this.activeWidget.config)
    }
  },
  methods: {
    ...mapMutations('screen', {
      activateWidget: ScreenMutations.ACTIVATE_WIDGET,
      removeWidget: ScreenMutations.REMOVE_WIDGET
    }),
    // Todo wrapper
    change (type, trigger = null) {
      switch (type) {
        case 'style':
          // 
          this.activeWidget.render.mergeOption(this.config)
          break
        case 'size':
          // wrapper
          const sizePreConfig = _.cloneDeep(this.activeWidget.config.commonConfig)
          const sizeMutation = {
            event: {
              distance: trigger === 'width'
                ? (this.config.commonConfig.width - sizePreConfig.width) * this.scale
                : (this.config.commonConfig.height - sizePreConfig.height) * this.scale,
              eventType: 'SINGLE',
              mouseType: 'mousemove',
              type: trigger === 'width' ? 'cr' : 'bc'
            },
            originalState: { ...sizePreConfig }
          }
          this.adjust({
            target: document.getElementById(this.activeWidget.widgetId),
            mutation: sizeMutation
          })
          this.adjust({
            target: document.getElementById('wrapper'),
            mutation: sizeMutation
          })
          break
        case 'position':
          // wrapper
          const positionPreConfig = _.cloneDeep(this.activeWidget.config.commonConfig)
          const positionMutation = {
            event: {
              direction: 'ANY',
              distance: 0,
              eventType: 'MOVE',
              mouseType: 'mousemove',
              position: {
                top: (this.config.commonConfig.top - positionPreConfig.top) * this.scale,
                left: (this.config.commonConfig.left - positionPreConfig.left) * this.scale
              },
              type: 'move'
            },
            originalState: { ...positionPreConfig }
          }
          this.adjust({
            target: document.getElementById(this.activeWidget.widgetId),
            mutation: positionMutation
          })
          this.adjust({
            target: document.getElementById('wrapper'),
            mutation: positionMutation
          })
          break
        default:
          break
      }
      // 
      this.updateActionWidget()
      // resize
      if (type === 'size') {
        const { config, render } = this.activeWidget
        if (config.type === 'Topology') {
          this.activeWidget.render.resize(
            render.container.offsetWidth,
            render.container.offsetHeight
          )
        }
        this.activeWidget.render.resize()
      }
    },
    /**
     * 
     */
    updateActionWidget () {
      const activeWidget = _.cloneDeep(this.activeWidget)
      this.activateWidget({
        widget: Object.assign(activeWidget, {
          config: this.config,
          render: this.activeWidget.render
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.common-template {
  height: calc(100vh - 224px);
  overflow: auto;
}
</style>
