/**
 * 
 */
class Range {
  constructor (min, max) {
    this.min = min
    this.max = max
  }
}

/**
 * 
 */
class TopologyIcon {
  constructor ({
    show = false,
    position = 'inside',
    name = 'network',
    width = 64,
    height = 64,
    img = ''
  }) {
    this.show = show
    this.position = position
    this.name = name
    this.width = width
    this.height = height
    this.img = img
  }
}

/**
 * 
 */
class BorderTypeOption {
  constructor (name, value) {
    this.name = name
    this.value = value
  }
}

/**
 * 
 */
class Border {
  constructor ({
    type = 'solid',
    color = '#333',
    width = 0
  }) {
    this.type = type
    this.typeOptions = [
      new BorderTypeOption('', 'none'),
      new BorderTypeOption('', 'solid')
    ]
    this.color = color
    this.width = width
  }
}

export { Range, TopologyIcon, Border }
