/**
 * 
 * @param orient 'horizontal' | 'vertical'
 * @param icon 'circle' | 'rect' | 'roundRect' | 'triangle' |  'diamond' | 'pin' | 'arrow' | 'none'
 */
class Legend {
  constructor ({
    show = false,
    orient = 'horizontal',
    icon = 'circle',
    top = 'auto',
    right = 'auto',
    bottom = 'auto',
    left = 'auto',
    textStyle = {}
  }) {
    this.show = show
    this.orient = orient
    this.top = top
    this.right = right
    this.bottom = bottom
    this.left = left
    this.icon = icon
    this.textStyle = new TextStyle(textStyle)
  }
}

/**
 * 
 * @param type 'solid' | 'dashed' | 'dotted'
 */
class LineStyle {
  constructor ({
    color = 'rgba(0,0,0,1)',
    width = 2,
    type = 'solid'
  }) {
    this.color = color
    this.width = width
    this.type = type
  }
}

/**
 * 
 * @param type  'single' | 'combination' | 'linear'
 * @param colorType  'default' | 'custom'
 */
class BarItemStyle {
  constructor ({
    type = 'single',
    colorType = 'default',
    colorScheme = 'default',
    color = 'rgba(7,171,253,1)',
    barBorderRadius = [0, 0, 0, 0]
  }) {
    this.type = type
    this.colorType = colorType
    this.colorScheme = colorScheme
    this.color = color
    this.barBorderRadius = barBorderRadius
  }

  getOption () {
    let color
    switch (this.type) {
      case 'single':
        color = this.color
        break
      case 'combination':
        color = [...this.color]
        break
      case 'linear':
        color = [...this.color].map(({ start, end }) => ({
          type: 'linear',
          x: 1,
          y: 0,
          x2: 1,
          y2: 1,
          colorStops: [
            {
              offset: 0,
              color: start
            },
            {
              offset: 1,
              color: end
            }
          ]
        }))
        break
      default:
        color = 'rgba(7,171,253,1)'
        break
    }
    return {
      color,
      barBorderRadius: this.barBorderRadius
    }
  }
}

/**
 * 
 * @param color 
 * @param fontStyle  'normal' | 'italic'
 * @param fontSize  12
 * @param fontWeight  'normal' | 'lighter' | 'bold' | 'bolder'
 */
class TextStyle {
  constructor ({
    color = 'rgba(0, 0, 0, 1)',
    fontStyle = 'normal',
    fontSize = 12,
    fontWeight = 'normal'
  }) {
    this.color = color
    this.fontStyle = fontStyle
    this.fontSize = fontSize
    this.fontWeight = fontWeight
  }
}

/**
 * 
 * @param show 
 * @param lineStyle 
 */
class AxisLine {
  constructor ({
    show = true,
    lineStyle = {}
  }) {
    this.show = show
    this.lineStyle = new LineStyle(lineStyle)
  }
}

/**
 * 
 * @param show: 
 * @param length: 
 * @param lineStyle 
 */
class AxisTick {
  constructor ({
    show = true,
    length = 5,
    lineStyle = {}
  }) {
    this.show = show
    this.length = length
    this.lineStyle = new LineStyle(lineStyle)
  }
}

/**
 * 
 * @param show 
 * @param rotate  -90 ~ 90
 * @param margin 
 */
class AxisLabel extends TextStyle {
  constructor ({
    show = true,
    rotate = 0,
    margin = 8,
    ...props
  }) {
    super(props)
    this.show = show
    this.rotate = rotate
    this.margin = margin
  }
}

/**
 * 
 * @param show 
 * @param lineStyle 
 */
class SplitLine {
  constructor ({
    show = false,
    lineStyle = {}
  }) {
    this.show = show
    this.lineStyle = new LineStyle(lineStyle)
  }
}

/**
 * 
 * @param aixsName =  'x' | 'y'
 * @param show 
 * @param position x 'bottom' | 'top'
 * @param type  'category' | 'value' | 'time'
 * @param name  'category' | 'value' | 'time'
 * @param nameLocation  'end' | 'center' | 'start'
 * @param nameTextStyle 
 * @param nameGap  15
 * @param axisLine 
 * @param axisTick 
 * @param axisLabel 
 * @param splitLine 
 */
class Aixs {
  constructor ({
    aixsName = '',
    show = true,
    type = 'category',
    name = '',
    nameLocation = 'end',
    nameTextStyle = {},
    nameGap = 15,
    gridIndex = 1,
    axisLine = {},
    axisTick = {},
    axisLabel = {},
    splitLine = {}
  }) {
    this.show = show
    this.type = type
    this.name = name
    this.nameLocation = nameLocation
    this.nameTextStyle = new TextStyle(nameTextStyle)
    this.nameGap = nameGap
    this.gridIndex = gridIndex
    this.axisLine = new AxisLine(axisLine)
    this.axisTick = new AxisTick(axisTick)
    this.axisLabel = new AxisLabel(axisLabel)
    this.splitLine = new SplitLine(splitLine)
  }
}

/**
 * x
 * : https://echarts.apache.org/zh/option.html#xAxis
 */
class XAixs extends Aixs {
  constructor ({
    position = 'bottom',
    ...props
  }) {
    super(props)
    this.aixsName = 'x'
    this.position = position
  }
}

/**
 * y
 * : https://echarts.apache.org/zh/option.html#yAxis
 */
class YAixs extends Aixs {
  constructor ({
    position = 'left',
    ...props
  }) {
    super(props)
    this.aixsName = 'y'
    this.position = position
  }
}

export {
  Legend,
  LineStyle,
  BarItemStyle,
  XAixs,
  YAixs
}
